import { DEFAULT_ADDRESS } from '../constants';
import { ethereumAddressRegex } from './regex';
export var getAvatarByAddress = function (address) {
    if (ethereumAddressRegex.test(address)) {
        return "https://effigy.im/a/".concat(address, ".svg");
    }
    else {
        return "https://effigy.im/a/".concat(DEFAULT_ADDRESS, ".svg");
    }
};
